











































































































































@use "~main/theme"

.prev-btn
  display: inline
  position: absolute
  left: 2%
  top: 40%
  opacity: 0.5

.next-btn
  display: inline
  position: absolute
  right: 2%
  top: 40%
  opacity: 0.5

.page-visiting
  color: rgb(var(--color-primary))

.page-unvisited
  color: rgb(var(--color-divider))
